import {LocationService} from "@/services/location_service"
import { Option } from "@/components/Forms/FieldInterface"
import { storageFactory } from "@/composables/storage"
import isEmpty from "lodash/isEmpty"

const offlineStorage = storageFactory()

export async function getFacilities(filter=''): Promise<Option[]> {
    let facilities = await offlineStorage.getFromStorage('locations', { name: filter })
    if (isEmpty(facilities)) facilities = await LocationService.getFacilities({name: filter})
    return facilities.filter((f: any) => !isEmpty(f) && f.name.trim() !='')
        .map((facility: any) => ({
            label: facility.name,
            value: facility.location_id,
            other: facility
        }))
}

export async function getLabFacilities(filter=''): Promise<Option[]> {
    const facilities = await LocationService.getLabs({'search_name': filter})
    return facilities.map((facility: any) => ({
            label: facility,
            value: facility
        }))
}

export async function getFacilityWards(filter=''): Promise<Option[]> {
    const wards = await LocationService.getFacilities({
        name: filter,
        tag: 'Facility adult sections'
    })

    return wards.map((ward: any) => ({
        label: ward.name,
        value: ward.name,
        other: ward
    }))
}

export async function getSpecialistClinics(): Promise<Option[]> {
    const clinics = await LocationService.getSpecialistClinics()
    return clinics.map((clinic: any) => ({
        label: clinic.name,
        value: clinic.name,
        other: clinic
    }))
}

export async function getRegions(): Promise<Option[]> {
    let regions = await offlineStorage.getFromStorage('regions', [])
    if (isEmpty(regions)) {
        regions = await LocationService.getRegions()
    }
    return regions.map((region: any) => ({
        label: region.name,
        value: region.region_id,
        other: region
    }))
}

export async function getDistricts(regionID: number): Promise<Option[]> {
    let districts = await offlineStorage.getFromStorage('districts', { 'region_id': regionID })
    if (isEmpty(districts)) districts = await LocationService.getDistricts(regionID)
    return districts.map((district: any) => ({
        label: district.name,
        value: district.district_id,
        other: district
    }))
}

export async function getTraditionalAuthorities(districtID: number, name=''): Promise<Option[]> {
    let TAs = await offlineStorage.getFromStorage('traditional_authorities', {
        district_id: districtID, name
    })
    if (isEmpty(TAs)) TAs = await LocationService.getTraditionalAuthorities(districtID, name)
    return TAs.map((TA: any) => ({
        label: TA.name,
        value: TA.traditional_authority_id,
        other: TA
    }))
}

export async function getVillages(traditionalAuthorityID: number, name=''): Promise<Option[]> {
    let villages = await offlineStorage.getFromStorage('villages', { 
        traditional_authority_id: traditionalAuthorityID, name
    })
    if (isEmpty(villages)) villages = await LocationService.getVillages(traditionalAuthorityID, name)
    return villages.map((village: any) => ({
        label: village.name,
        value: village.village_id,
        other: village
    }))
}
