import { StorageInterface } from '@/interfaces/storage';
import { filter, isInteger, toLower } from 'lodash';

const INDEX_STORE = 'openmrs'
const DB_NAME = 'offline_db'

type StorageName = "LocalStorage" | "IndexedDBStorage";

export const storageFactory = (storageName: StorageName='IndexedDBStorage'): StorageInterface => {
  return eval(`new ${storageName}()`) as StorageInterface;
};

export class LocalStorage implements StorageInterface {
  existsinStorage(key: string): Promise<boolean> {
    const data = localStorage.getItem(key);
    if (data) {
      return Promise.resolve(true);
    }
    return Promise.resolve(false);
  }

  getFromStorage(key: string): Promise<any> {
    return new Promise((resolve, reject) => {
      const data = localStorage.getItem(key);
      if (data) {
        return resolve(JSON.parse(data));
      }
      resolve([]);
    });
  }

  deleteFromStorage(key: string): boolean {
    localStorage.removeItem(key);
    return true;
  }

  saveToStorage(key: string, data: JSON): boolean {
    localStorage.setItem(key, JSON.stringify(data));
    return true;
  }
}

export class IndexedDBStorage implements StorageInterface {
  db!: IDBDatabase;

  constructor() {
    const openRequest = indexedDB.open(DB_NAME, 1);
    openRequest.onupgradeneeded = function () {
      openRequest.result.createObjectStore(INDEX_STORE);
    };
    openRequest.onsuccess = () => {
      this.db = openRequest.result;
    };
  }

  existsinStorage(key: string): Promise<unknown> {
    return new Promise((resolve, reject) => {
      const request = this.db
        .transaction(INDEX_STORE)
        .objectStore(INDEX_STORE)
        .get(key);
      request.onsuccess = () => {
        resolve(request.result);
      };
      request.onerror = () => {
        reject(request.error);
      };
    });
  }

  getFromStorage(key: string, filters: any): Promise<any> {
    return new Promise((resolve, reject) => {
      const request = this.db
        .transaction(INDEX_STORE)
        .objectStore(INDEX_STORE)
        .openCursor();
      request.onsuccess = () => {
        const cursor = request.result;
        if (cursor) {
          if (cursor.key === key) {
            if (filters) {
              resolve(filteredData(cursor, filters));
            } else {
              resolve(cursor.value.data);
            }
          }
          cursor.continue();
        } else {
          resolve([]);
        }
      };
      request.onerror = () => {
        reject("error");
      };
    });
  }

  saveToStorage(key: string, data: any): boolean {
    this.db
      .transaction(INDEX_STORE, "readwrite")
      .objectStore(INDEX_STORE)
      .put(data, key);
    return true;
  }

  deleteFromStorage(key: string): boolean {
    this.db
      .transaction(INDEX_STORE, "readwrite")
      .objectStore(INDEX_STORE)
      .delete(key);
    return true;
  }
}

function filteredData(cursor: IDBCursorWithValue, filters: any): any {
  return filter(cursor.value, (item: any) => {
    return Object.entries(filters).every(([key, value]: any) => {
      if (isInteger(item[key])) {
        return !value || item[key] === value;
      } else {
        return !value || toLower(item[key]).includes(toLower(value));
      }
    });
  });
}

