
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { HtsArtReferralOutcomeService } from "@/apps/HTS/services/hts_art_referral_outcome_service"
import { getFacilities } from '@/utils/HisFormHelpers/LocationFieldOptions'
import { generateDateFields } from '@/utils/HisFormHelpers/MultiFieldDateHelper'
import { ProgramService } from '@/services/program_service'

export default defineComponent({
    components: { IonPage },
    mixins: [EncounterMixinVue],
    data: () => ({
      service: {} as HtsArtReferralOutcomeService
    }),
    watch: {
      ready: {
        handler(ready: boolean) {
          if (ready) {
            this.service = new HtsArtReferralOutcomeService(this.patientID, this.providerID)
            this.fields = [
              this.artStatusField(),
              this.artReferralOutcomeField(),
              ...this.artOutcomeDateFields(),
              this.artSiteField(),
              this.hasArvNumberField(),
              this.arvNumberField()
            ]
          }
        },
        immediate: true
      }
    },
    methods: {
      onFinish(formData: any, computedData: any) {
        this.saveFormEncounterAndProceed({
          encounterTypeID: this.service.encounterTypeID,
          formData,
          computedData,
        })
      },
      artStatusField(): Field {
        let available = false
        let optionData: Option[] = []
        let linkDataSet = false
        return {
          id: 'on_art',
          helpText: 'ART Summary',
          type: FieldType.TT_HIDDEN,
          init: async () => {
            try {
              const hts = await ProgramService.getProgramInformation(this.patientID)
              linkDataSet = hts && /Linked|art/i.test(hts.art_outcome)
              const art: any = await ProgramService.getProgramSummary(this.patientID, 1)
              available = [
                !/unknown/.test(art.current_outcome),
                !/n\/a/i.test(art.art_start_date)
              ].every(Boolean)
              if (!available) return true
              optionData = [
                {
                  label: 'Outcome',
                  value: art.current_outcome,
                  other: {
                    obs: () => this.service.buildValueCoded('Antiretroviral status or outcome', 'Linked')
                  }
                },
                {
                  label: 'Outcome date',
                  value: art.art_start_date,
                  other: {
                    obs: () => this.service.buildValueDate('Outcome date', art.art_start_date)
                  }
                },
                {
                  label: 'Facility',
                  value: ProgramService.getLocationName(),
                  other: {
                    obs: () => this.service.buildValueText('ART clinic location', `${ProgramService.getLocationName()}`)
                  }
                },
                {
                  label: 'ARV Number',
                  value: art.arv_number,
                  other: {
                    obs: () => this.service.buildValueText('Art number', art.arv_number)
                  }
                }
              ]
            } catch (e) {
              console.error(e)
            }
            return true
          },
          summaryMapValue: (v: Option) => {
            return v
          },
          condition: () => available,
          defaultValue() {
            return optionData
          },
          finalComputedValue: (v: Option[]) => {
            if (!linkDataSet) {
              return {
                obs: v.filter(d => typeof d.other.obs === 'function').map(d => d.other.obs())
              }
            }
          },
          options: () => {
            return optionData
          }
        }
      },
      artReferralOutcomeField(): Field {
        return {
          id: "outcome",
          helpText: 'ART Outcome Referral',
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => f.on_art === null,
          finalComputedValue: (v: Option) => {
            return {
              obs: this.service.buildValueCoded('Antiretroviral status or outcome', v.value as string) 
            }
          },
          options: () => {
            return this.mapStrToOptions([
                'Linked',
                'Refused',
                'Died',
                'Already on ART',
                'Unknown'
            ])  
          }
        }
      },
      artSiteField(): Field {
        return {
          id: 'art_site',
          helpText: 'Visited Clinic Name',
          type: FieldType.TT_SELECT,
          isRequired: () => true,
          condition: (f: any) => /Linked|Already on art/i.test(f.outcome.value),
          finalComputedValue: (v: Option) => ({
            obs: this.service.buildValueText('ART clinic location', v.label)
          }),
          options: (_: any, filter='') => getFacilities(filter),
          config: {
            footerBtns: [
              {
                name: "Here",
                slot: "end",
                onClickComponentEvents: {
                  setValue: () => {
                    return HtsArtReferralOutcomeService.getLocationName()
                  }
                },
                onClick: () => null
              }
            ],
            showKeyboard: true,
            isFilterDataViaApi: true
          }
        }
      },
      artOutcomeDateFields(): Field[] {
        return generateDateFields({
          id: 'outcome_date',
          helpText: 'Outcome',
          required: true,
          minDate: () => this.facts.birthDate,
          maxDate: () => this.service.date,
          condition: (f: any) => !/unknown/i.test(f.outcome.value),
          computeValue: (date: string) => {
            return {
                date,
                obs: this.service.buildValueDate('Outcome date', date)
            }
          },
          estimation: {
            allowUnknown: false
          }
        })
      },
      hasArvNumberField(): any {
        return {
          id: 'has_arv_number',
          helpText: 'ARV Number available',
          type: FieldType.TT_YES_NO,
          isRequired: () => true,
          condition: (f: any) => /linked|already on art/i.test(f.outcome.value),
          options: () => [
            {
              label: "ARV number available?",
              values: this.yesNoOptions()
            }
          ]
        }
      },
      arvNumberField(): Field {
        return {
          id: 'arv_number',
          helpText: 'ARV Number',
          type: FieldType.TT_TEXT,
          isRequired: () => true,
          condition: (f: any) => f.has_arv_number === 'Yes',
          finalComputedValue: (v: Option) => {
            return {
                obs: this.service.buildValueText('Art number', `${v.value}`)
            }
          }
        }
      }
    }
})
